import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);



import menu from './modules/menu.js';
import services from './modules/services.js';

export const store = new Vuex.Store({
	state: {
		count: 0,
		headerer: '  ',
		subheaderer: '      ',
		tel: '+7-999-777-5-444',
		baseName: '  ',
		address: ' ,  -, ., . 19'
	},
	modules: {
		menu,
		services
	},
	getters: {
		COUNT: state => {
			return state.count+2;
		}
	}
})



